/* --------- */
/*Scrollbar   */
/* --------- */

::-webkit-scrollbar {
    width: 7px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #303b35;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #4f5e55;
    border-radius: 12px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #1f1e1d;
    border-radius: 12px;
  }



